.popup {
	//  visibility: hidden;
	//  opacity: 0;
	// display: none;
	//  position: absolute;
	//  top: 60px;
	//  left: 0;
	//  left: -300px;
	// 	transform: translateX(-10%);

   visibility: visible;
   opacity: 1;
   background-color: rgba(0,0,0,0.8);
   position: fixed;
   top:0;
   left:0;
   right:0;
   bottom:0;
   margin:0;
   z-index: 5;
   transition: all ease 1s;
}
.popup-contenedor {
   position: relative;
	 top:50%;
	 left:50%;
	 transform: translate(-50%, -50%);
   padding: 45px 30px;
   background-color: $backgroundColorPopupContenedor;
   color:$colorPopupContenedor;
	 font-weight: bold;
   border-radius: 3px;
   width:60%;
}
.popup-close {
	 border: none;
   position: absolute;
	 display: block;
   top:3px;
   right:3px;
   background-color: $backgroundColorPopupClose;
   padding:7px 10px;
   font-size: 20px;
   text-decoration: none;
   line-height: 1;
   color:#fff;
	 border-radius: 3px;
}
